.top-search-bar .loading-spinner {
	display: inline-block;
	position: relative;
	margin-left: 280px;
	padding: 15px;
	width: 40px;
	height: 40px;
}

.top-search-bar .loading-spinner div {
	box-sizing: border-box;
	display: block;
	position: absolute;
	width: 32px;
	height: 32px;
	margin: 4px;
	border: 4px solid rgb(61, 61, 61);
	border-radius: 50%;
	-webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
	        animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
	border-color: rgb(61, 61, 61) transparent transparent transparent;
}

.top-search-bar .loading-spinner div:nth-child(1) {
	-webkit-animation-delay: -0.45s;
	        animation-delay: -0.45s;
}

.top-search-bar .loading-spinner div:nth-child(2) {
	-webkit-animation-delay: -0.3s;
	        animation-delay: -0.3s;
}

.top-search-bar .loading-spinner div:nth-child(3) {
	-webkit-animation-delay: -0.15s;
	        animation-delay: -0.15s;
}

@-webkit-keyframes lds-ring {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes lds-ring {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

