@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans&family=Merriweather+Sans:ital,wght@0,300;0,400;0,600;0,700;1,300;1,400;1,600;1,700&display=swap');

main {
	margin: 0 auto;
	width: 100%;
	position: relative;
}

ul {
	padding: 0px !important;
}

body {
	width: 100%;
	height: 100%;
	margin: 0 auto;
}

/*--------------------------------------------------------------------------------------------------------*/

.title {
	font-family: 'Lucida Console', 'Courier New', monospace;
	width: 70%;
	margin: auto;
}

.product-first-block {
	width: 70%;
	margin: auto;
}

.product-first-block img {
	width: 400px;
	height: 400px;
	margin: auto;
}

.product-first-block h2 {
	font-family: 'Lucida Console', 'Courier New', monospace;
	font-size: 15px;
	width: 400px;
	height: 20px;
	margin: auto;
	margin-top: -360px;
}

.product-first-block p {
	font-family: 'Lucida Console', 'Courier New', monospace;
	font-size: 13px;
	width: 400px;
	height: 400px;
	margin: auto;
}

.seller-box {
	border: 1px solid #000000;
	border-radius: 20px;
	background-color: #cacaca;
	width: 350px;
	height: 515px;
	margin: auto;
	margin-left: 1000px;
	margin-top: -460px;
}

.seller-box img {
	margin-left: 50px;
	margin-top: 20px;
	width: 250px;
	height: 250px;
}

.message {
	width: 30px;
	height: 30px;
}

.message img {
	margin-left: 50px;
	margin-top: 30px;
	width: 30px;
	height: 30px;
}

.message p {
	font-family: 'Lucida Console', 'Courier New', monospace;
	font-size: 12px;
	margin-left: 90px;
	margin-top: -26px;
}

.box-message button {
	border: 2px solid #000000;
	background-color: transparent;
	width: 290px;
	height: 35px;
	border-radius: 20px;
	margin-top: -60px;
	margin-left: 40px;
}

.seller-box h3 {
	font-family: 'Lucida Console', 'Courier New', monospace;
	font-size: 13px;
	font-size: medium;
	margin-top: 20px;
	margin-left: 30px;
}

.value {
	font-family: 'Lucida Console', 'Courier New', monospace;
	font-size: 15px;
	margin-top: -100px;
	height: 35px;
	width: 200px;
}

.cart {
	font-family: 'Lucida Console', 'Courier New', monospace;
	height: 30px;
	width: 210px;
	padding-left: 10%;
	padding-top: 5%;
	border: 4px solid #000000;
	background-color: #7dd359;
	border-radius: 40px;
}
