@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans&family=Merriweather+Sans:ital,wght@0,300;0,400;0,600;0,700;1,300;1,400;1,600;1,700&display=swap');

.header ul {
	padding: 0px !important;
}

.header-homepage {
	display: inline;
	width: 100%;
	background-color: #7dd359;
	height: 87px;
}

.header {
	display: flex;
	width: 100%;
	align-items: center;
	background-color: #000000;
	height: 83px;
	overflow-y: visible;
}

.header .logo {
	width: 5%;
	float: left;
	/*border: 1px solid #7DD359;*/
}

.header .profile ul li {
	display: grid;
	float: center;
}

.header .profile {
	padding: auto;
	width: 50px;
	height: 30px;
}

.header .dropdown {
	position: relative;
	display: table;
}

.header .dropdown img {
	margin-bottom: 15px;
}

.header .profile-menu {
	font-family: 'Lucida Console', 'Courier New', monospace;
	font-size: auto;
	display: none;
	position: absolute;
	left: -140px;
	top: 80px;
	background-color: #000000;
	min-width: 160px;
	box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
	z-index: 1;
}

.header .profile-menu a,
.header .profile-menu Link {
	color: white;
	padding: 12px 16px;
	text-decoration: none;
	display: block;
	background-color: #000000;
	border-radius: 5px;
}

.header .profile-menu a:hover {
	background-color: #000000;
}

.header .dropdown:hover .profile-menu,
.header .profile-menu:hover {
	display: block;
	background-color: #000000;
	border-left: 1px solid #7dd359;
	border-right: 1px solid #7dd359;
	border-bottom: 1px solid #7dd359;
	border-bottom-left-radius: 5px;
	border-bottom-right-radius: 5px;
}

.header .menu {
	width: auto;
	margin-left: 140px;
	display: flex;
}

.header .menu ul li {
	font-family: 'Lucida Console', 'Courier New', monospace;
	display: inline-block;
	color: #ffffff;
	margin-left: 15px;
	border: 1px solid #000000;
}

.header .menu ul li:hover {
	height: auto;
	color: #7dd359;
	border-top: 2px solid #7dd359;
	border-bottom: 2px solid #7dd359;
}

.header .menu ul li a:hover {
	color: #7dd359;
	border-top: 1px solid #7dd359;
	border-bottom: 1px solid #7dd359;
}

.header .menu ul li a {
	color: #ffffff;
	text-decoration: none;
	font-size: 16px;
	text-transform: uppercase;
}

.header .profile-menu a {
	border-radius: 0;
}

.header .profile-menu a:hover {
	background-color: #7dd359;
}
